import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { Text } from "common/components/Text";
import { PatientCommentShield } from "components/PatientCommentShield";

import { PatientFeedbackTextProps } from "./PatientFeedbackText.types";
import styles from "./PatientFeedbackText.module.scss";

const cx = classNames.bind(styles);

const PatientFeedbackText: FunctionComponent<PatientFeedbackTextProps> = (
    props
) => {
    const {
        feedbackText,
        patientName,
        treatmentDuration = "",
        className,
    } = props;

    return (
        <div className={cx("root", className)}>
            <PatientCommentShield
                className={cx("feedback-head")}
                name={patientName}
                description={treatmentDuration}
            />
            <Text className={cx("feedback-text")}>{feedbackText}</Text>
        </div>
    );
};

export default PatientFeedbackText;
