import { FC, useState } from "react";
import classNames from "classnames/bind";
import Carousel from "react-multi-carousel";
import Head from "next/head";

import { Picture } from "common/components/Picture";
import { PatientFeedbackText } from "components/PatientFeedbackText";

import { PatientFeedbackSliderProps } from "./PatientFeedbackSlider.types";
import styles from "./PatientFeedbackSlider.module.scss";

const cx = classNames.bind(styles);

const PatientFeedbackSlider: FC<PatientFeedbackSliderProps> = ({
    patientFeedbacks,
    className,
}) => {
    const responsive = {
        mobile: {
            // TODO https://github.com/smile2impress/frontend/issues/176
            breakpoint: { max: 880, min: 0 },
            items: 1,
            partialVisibilityGutter: 24,
        },
    };

    const [isLastSlide, setIsLastSlide] = useState(false);

    return (
        <>
            {/* TODO https://github.com/smile2impress/frontend/issues/176 */}
            <Head>
                <link
                    rel="stylesheet"
                    type="text/css"
                    href="https://cdn.jsdelivr.net/npm/react-multi-carousel@2.5.5/lib/styles.min.css"
                />
            </Head>
            <div
                className={cx("root", className, {
                    "root_with-margin": isLastSlide,
                })}
            >
                <Carousel
                    responsive={responsive}
                    partialVisible={true}
                    arrows={false}
                    showDots
                    beforeChange={(nextSlide) => {
                        setIsLastSlide(
                            nextSlide === patientFeedbacks.length - 1
                        );
                    }}
                >
                    {patientFeedbacks.map((patientFeedback, index) => (
                        <div className={cx("item")} key={index}>
                            <Picture
                                classNameImg={cx("image", "image_with-margin")}
                                {...patientFeedback.patientImage}
                            />
                            <PatientFeedbackText
                                className={cx("patient-feedback-text")}
                                {...patientFeedback}
                            />
                        </div>
                    ))}
                </Carousel>
            </div>
        </>
    );
};

export default PatientFeedbackSlider;
