import { FC } from "react";
import classNames from "classnames/bind";

import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Picture } from "common/components/Picture";
import { Text } from "common/components/Text";

import { AdvantagesProps } from "./Advantages.types";
import styles from "./Advantages.module.scss";

const cx = classNames.bind(styles);

export const Advantages: FC<AdvantagesProps> = (props) => {
    const { list = [], className } = props;

    return Boolean(list.length) ? (
        <section className={cx("root", className)}>
            <div className={cx("row")}>
                {list.map((item) => {
                    const { image, title, content } = item;

                    return (
                        <div className={cx("column")} key={title}>
                            <Picture className={cx("image")} {...image} />
                            <Heading
                                tag={HeadingTag.H3}
                                type={HeadingType.M}
                                className={cx("title")}
                            >
                                {title}
                            </Heading>
                            <Text className={cx("content")}>{content}</Text>
                        </div>
                    );
                })}
            </div>
        </section>
    ) : null;
};
