import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { PatientFeedback } from "components/PatientFeedback";

import { PatientFeedbackListProps } from "./PatientFeedbackList.types";

import styles from "./PatientFeedbackList.module.scss";

const cx = classNames.bind(styles);

const PatientFeedbackList: FunctionComponent<PatientFeedbackListProps> = (
    props
) => {
    const { patientFeedbacks, className } = props;

    return (
        <div className={cx("root", className)}>
            {patientFeedbacks.map((feedback, index) => (
                <div key={index} className={cx("item")}>
                    <PatientFeedback
                        className={cx("patient-feedback-item")}
                        {...feedback}
                    />
                </div>
            ))}
        </div>
    );
};

export default PatientFeedbackList;
