import { FC } from "react";
import classNames from "classnames/bind";
import NextHead from "next/head";

import { getAnalyticsId } from "common/utils/AnalyticsId";
import { Text, TextTag } from "common/components/Text";
import {
    Heading,
    HeadingTag,
    HeadingTheme,
} from "common/components/new/Heading";
import { Button, ButtonSize } from "common/components/new/Button";
import { Container } from "common/components/Container";
import { HeaderShort } from "common/components/new/HeaderShort";
import { Theme } from "common/constants/enum";
// import { checkWebPFeature } from "utils/checkWebPFeautre";

import { HeadProps } from "./Head.types";
import styles from "./Head.module.scss";

const cx = classNames.bind(styles);

export const Head: FC<HeadProps> = ({
    title,
    button,
    contacts,
    isMailPage,
    subtitle,
    hasMargin = false,
    description,
    isFixedButtonActive,
    className,
    theme = Theme.LIGHT,
}) => {
    // const [supportWebP, setSupportWebP] = useState(true);

    // useEffect(() => {
    //     setSupportWebP(checkWebPFeature());
    // }, []);

    const supportWebP = false;

    return (
        <section
            className={cx("root", hasMargin && "root_margin", className, {
                "no-webp": !supportWebP,
            })}
        >
            <NextHead>
                <link
                    rel="preload"
                    as="image"
                    href="/special/images/new/universal-spain/bg-head-mobile.jpg"
                />
            </NextHead>
            <HeaderShort
                {...contacts}
                button={button}
                isFixedButtonActive={isFixedButtonActive}
                theme={theme}
                isAbsolute
                isMailPage={isMailPage}
            />
            <Container className={cx("container")}>
                <div className={cx("column")}>
                    <div>
                        <div className={cx("content")}>
                            <Heading
                                tag={HeadingTag.H2}
                                theme={HeadingTheme.White}
                                className={cx("title")}
                            >
                                {title}
                            </Heading>
                            {subtitle && (
                                <Text
                                    tag={TextTag.H2}
                                    className={cx("subtitle")}
                                >
                                    {subtitle}
                                </Text>
                            )}
                        </div>

                        <Button
                            {...button}
                            id={getAnalyticsId({ zone: "head" })}
                            className={cx("button")}
                            size={ButtonSize.M}
                            onClick={button?.onClick}
                        />

                        {description && (
                            <Text className={cx("description")}>
                                {description}
                            </Text>
                        )}
                    </div>
                </div>
            </Container>
        </section>
    );
};
