import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { PatientUserIconProps } from "./PatientUserIcon.types";

import styles from "./PatientUserIcon.module.scss";

const cx = classNames.bind(styles);

const PatientUserIcon: FunctionComponent<PatientUserIconProps> = (props) => {
    const { fallbackLetter, className } = props;

    return <div className={cx("root", className)}>{fallbackLetter}</div>;
};

export default PatientUserIcon;
