import { FC } from "react";
import classNames from "classnames/bind";

import { Text } from "common/components/Text";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { PatientUserIcon } from "components/PatientUserIcon";

import { PatientCommentShieldProps } from "./PatientCommentShield.types";
import styles from "./PatientCommentShield.module.scss";
import { getNameFirstLetter } from "./helpers";

const cx = classNames.bind(styles);

const PatientCommentShield: FC<PatientCommentShieldProps> = ({
    name,
    description,
    className,
}) => {
    const nameFirstLetter = getNameFirstLetter(name);

    return (
        <div className={cx("root", className)}>
            <PatientUserIcon
                className={cx("icon")}
                fallbackLetter={nameFirstLetter}
            />
            <div className={cx("text-section")}>
                <Heading
                    type={HeadingType.M}
                    tag={HeadingTag.H3}
                    className={cx("name")}
                >
                    {name}
                </Heading>
                {description && (
                    <Text className={cx("description")}>{description}</Text>
                )}
            </div>
        </div>
    );
};

export default PatientCommentShield;
