import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { Picture } from "common/components/Picture";
import { PatientFeedbackText } from "components/PatientFeedbackText";

import { PatientFeedbackProps } from "./PatientFeedback.types";

import styles from "./PatientFeedback.module.scss";

const cx = classNames.bind(styles);

const PatientFeedback: FunctionComponent<PatientFeedbackProps> = (props) => {
    const {
        patientImage,
        patientName,
        feedbackText,
        treatmentDuration,
        reverse = false,
        className,
    } = props;

    return (
        <div className={cx("root", className, { reverse })}>
            <div className={cx("patient-image")}>
                <Picture
                    {...patientImage}
                    classNameImg={cx("patient-image__image")}
                />
            </div>
            <PatientFeedbackText
                className={cx("patient-feedback-text")}
                treatmentDuration={treatmentDuration}
                patientName={patientName}
                feedbackText={feedbackText}
            />
        </div>
    );
};

export default PatientFeedback;
